
.list_item {
  position: relative;
  width: 100%;
  height: 110px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  background: white;
  border-radius: 10px;
  margin: 10px 0;
}
.list_item .goods_img {
  width: 100px;
  height: 100%;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.list_item .goods_img img {
  width: 100%;
  height: 100%;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.list_item .goods_info {
  width: 70%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  font-size: 15px;
}
.list_item .goods_info .goods_name {
  width: 100%;
  height: 42px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-left: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
@media only screen and (max-width:320px){
.list_item .goods_info .goods_name{
        font-size: 14px;
        height: 40px;
}
}
.list_item .goods_info .goods_price {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-left: 10px;
}
.goods_info .goods_price .now_price {
  font-size: 32rpx;
  color: red;
  margin-right: 10px;
}
.goods_info .goods_price .before_price {
  font-size: 26rpx;
  color: #ccc;
  text-decoration: line-through;
}
.goods_info .goods_coupon {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 70px;
  height: 25px;
  line-height: 25px;
  margin-bottom: 4px;
  margin-left: 10px;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #ec4e4f;
  border-radius: 5px;
  color: #ec4e4f;
}
.goods_info .goods_coupon .coupon_name {
  width: 25px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  border-right: 1px dotted #ec4e4f;
  margin-right: 10px;
}
.list_item .iconfont{
    position: absolute;
  color: #e94f4f;
  font-size: 30px;
  right: 25px;
  bottom: 10px;
  width: 50px;
  height: 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}



