
.goods .rule_box {
  background-color: white;
  border: 2px solid #ffde37;
  padding: 10px 10px 10px 8px;
  border-radius: 10px;
  margin:10px;
}
.goods .rule_box span {
  font-size: 14px;
}
.goods .verify {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 10px;
  padding: 5px 0;
  background-color: #fde43d;
  color: #774024;
  border-radius: 50px;
  font-size: 15px;
}
.verify .tip {
  font-size: 12px;
}
.goods .goods_list {
  margin: 10px;
  padding-bottom: 50px;
}
.goods .goods_list .bottom_tip {
  padding: 5px 0;
  color: white;
  text-align: center;
}
.goods .van-tabs__line {
  background: #fde43d;
}
.goods .van-tabs .van-tab {
  background: #ec4e4f;
  color: white;
}
.goods .van-tabs__nav {
  background: transparent;
}
.goods .van-tab.van-tab--active {
  color: #fde43d;
}
.goods .van-hairline--top-bottom::after {
  border-width: 0;
}
.goods .type_nav {
  width: 100%;
  overflow: hidden;
}
.goods .type_nav .nav_list {
  list-style: none;
  margin: 0;
  padding: 0 0 0 5px;
  white-space: nowrap;
}
.goods .type_nav .nav_list li {
  list-style: none;
  display: inline-block;
  padding: 5px;
  color: white;
  text-align: center;
  margin-right: 6px;
}
.goods .type_nav .nav_list li.active {
  color: #fde43d;
  border-bottom: 3px solid #fde43d;
}
@media only screen and (max-width: 320px) {
.goods .rule_box {
    margin: 10px 5px;
    padding: 8px;
}
.goods .rule_box span {
    font-size: 13px;
}
.goods .verify {
    margin: 8px;
    border-radius: 40px;
}
.goods .goods_list {
    margin: 10px 5px;
}
}



