
.home .tip_text {
  width: 100%;
  padding: 5px 0;
  text-align: center;
  color: white;
}
.home_swiper {
  width: 100%;
  height: 180px;
}
.home .bannerTop {
  width: 100%;
  height: 180px;
}
.home .bannerTop img {
  width: 100%;
  height: 100%;
}
.home_swiper .swiper-container {
  margin: 0;
  height: 100%;
}
.home_swiper .swiper-slide img {
  width: 100%;
  height: 100%;
}
.home_nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 10px;
  padding: 0 15px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.home_nav > div {
  width: 33%;
  height: 36px;
}
@media only screen and (max-width: 320px) {
.home_nav {
    padding: 0 10px;
}
.home_nav > div {
    height: 32px;
}
}
.home_nav > div span {
  display: block;
  width: 100%;
  height: 100%;
}
.home_nav > div span img {
  width: 100%;
  height: 100%;
}
.home_rule {
  width: 100%;
  padding: 10px 0;
  text-align: center;
  color: #ffde37;
  font-size: 12px;
}
.home .cart {
  z-index: 100;
  position: fixed;
  bottom: 60px;
  right: 25px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.8);
}
.home .cart .iconfont {
  color: white;
  font-size: 20px;
}
.home .cart .count {
  position: absolute;
  top: -5px;
  right: -5px;
  display: block;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  line-height: 25px;
  font-size: 14px;
  background: #ffde37;
  text-align: center;
}



