
.rules{
  padding-bottom: 50px;
}
.rules .rule_item {
  margin: 10px;
  padding: 20px;
  border-radius: 10px;
  background: white;
}
.rules .rule_title {
  color: #ec4e4f;
  margin-bottom: 8px;
}
.rules .rule_content {
  font-size: 13px;
}
@media only screen and (max-width: 320px) {
.rules .rule_item {
    margin: 0px 5px 5px;
    padding: 10px;
    border-radius: 10px;
}
}



