
.bind .invite_code,
.bind .input_info {
  margin: 10px;
  background: white;
  padding: 10px 0 10px;
  font-size: 16px;
}
.bind .invite_code span {
  margin-left: 100px;
  color: #774024;
}
.bind .tip {
  position: relative;
  margin-left: 15px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.bind .tip::after {
  content: "";
  display: block;
  width: 3px;
  height: 100%;
  background: #ffde37;
  position: absolute;
  top: 0;
  left: -5px;
}
.bind .tip .text1 {
  color: #ec4e4f;
  font-size: 12px;
  margin-right: 8px;
}
.bind .tip .text2 {
  color: #999;
  font-size: 12px;
}
.bind .input_info .form {
  margin: 20px 20px 12px;
}
.bind .input_info .form > .text {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
  height: 40px;
  background: #eee;
  margin-bottom: 10px;
}
.bind .input_info .form .inputType {
  margin-left: 10px;
  width: 30%;
}
.bind .input_info .form input {
  width: 64%;
  font-size: 14px;
  background-color: #eee;
  border: none;
  outline: none;
}
.bind .input_info .form .save_btn {
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: white;
  background: #3f93e6;
  border-radius: 5px;
}
.bind .input_info .warning {
  margin-left: 10px;
  font-size: 12px;
  color: #ec4e4f;
}
@media only screen and (max-width: 320px) {
.bind .invite_code,
  .bind .input_info {
    margin: 0 5px 10px;
}
.bind .invite_code span {
    margin-left: 90px;
}
.bind .tip {
    margin-left: 10px;
    font-size: 14px;
}
.bind .tip .text1 {
    margin-right: 5px;
}
.bind .input_info .form {
    margin: 10px;
}
}



