
.tabbar{
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 50px;
    z-index: 101;
    background: white;
}
.tabbar ul,
.tabbar li{
    list-style: none;
    margin: 0;
    padding: 0;
}
.tabbar ul{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    width: 100%;
    height: 50px;
}
.tabbar li{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 100%;
    height: 50px;
    color: #666;
    cursor: pointer;
}
.tabbar li .type{
    font-size: 14px;
}
.tabbar li .iconfont{
    font-size: 20px;
}



