
.order .tip_text {
  width: 100%;
  padding: 5px 0;
  text-align: center;
  color: white;
}
.order .order_detail {
  margin: 10px;
  background: white;
  padding: 10px 0 10px;
  font-size: 16px;
}
.order_detail .title {
  position: relative;
  margin-left: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.order_detail .title::after {
  content: "";
  display: block;
  width: 3px;
  height: 100%;
  background: #ffde37;
  position: absolute;
  top: 0;
  left: -5px;
}
.order_detail .subtitle {
  margin-left: 20px;
  color: #999;
  font-size: 12px;
}
.order_detail .detail {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 10px 0 15px;
}
.order_detail .detail .item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.order_detail .detail .num {
  font-size: 17px;
  color: #774024;
}
.order_detail .detail .desc {
  font-size: 12px;
  color: #999;
}
.order_detail .warning {
  font-size: 12px;
