}
.order_list {
  margin: 10px;
  background: white;
  font-size: 16px;
  margin-bottom: 52px;
}
.order_list .no_order {
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
}
.order_list .order_item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 10px;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #ccc;
}



